*{
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	outline: none;
	text-decoration: none;
}

body {
	background-color: linen;
}

::-webkit-scrollbar {
  	height: 8px;
	width: 5px;
} 
::-webkit-scrollbar-track {
	background-color: white;
} 
::-webkit-scrollbar-thumb {
	background-color: dimgray; 
	border-radius: 30px;
} 
::-webkit-scrollbar-button,
::-webkit-scrollbar-corner {
  	display: none;
}